import { Component, OnInit } from '@angular/core';
import { PrintServiceService } from '../../services/print-service.service';
import { Proceso, ESTADO } from 'src/model/proceso';
import { Page } from 'src/app/generic-table/shared/page';
import { ProcesoService } from 'src/services/tarea.service';
import { MatDialog } from '@angular/material/dialog';
import { MatTableDataSource } from '@angular/material/table';
import { Pageable } from 'src/model/pageable';
import { Observable } from 'rxjs';
import { AbmTareasComponent } from './abm-tareas/abm-tareas.component';
import { GenericTable } from '../generic-table/shared/generic-table';
import { SortDirection } from '../generic-table/shared/sort-direction';
import { GenericTableFilter } from '../generic-table/shared/GenericTableFilter';
import { RowAction } from '../generic-table/shared/row-action';
import { PrintData } from '../generic-table/table-print/print-data';
import { Title } from '@angular/platform-browser';
import { TableFilter } from '../generic-table/shared/table-filter';
import { TagsService } from 'src/services/tags.service';

export interface PeriodicElement {
  name: string;
  position: number;
  weight: number;
  symbol: string;
}

@Component({
  selector: 'app-tareas',
  templateUrl: './tareas.component.html',
  styleUrls: ['./tareas.component.css']
})
export class TareasComponent extends GenericTable<Proceso>  implements OnInit  {
  
  public rowActions : RowAction[];
  public printData : PrintData;

  constructor(
    private tareaService : ProcesoService,
    private tagService : TagsService,
    private printService: PrintServiceService,
    private dialog : MatDialog) {
      super(Proceso);

      //custom actions for table
      this.rowActions = [
        {icon: "timeline",
        name : "Ver movimientos",
        tooltip : "Ver movimientos del trbajo",
        click : (e)=>{console.log(e);}
      }
      ]
      //custom print data
      this.printData = new PrintData();
      this.printData.title = "LISTADO DE TRABAJOS";
      this.printData.details.set("Detalles","Se muestra lista de trabajos filtrados segn los siguientes campos aplicados.");

    }

    ngOnInit() {

    }
    searchFetch(search: string, resultsSize: number, pageIdx: number, sort?: SortDirection, filters?: GenericTableFilter): Observable<Page<Proceso>> {
      let extraParams = new Map<string,string>();
      filters.multipleOptions.forEach((val:TableFilter[],key )=>{
        extraParams
        .set(key,val.filter(filter=>filter.value==true)
          .map(obj=>obj.name)
          .join(',')
        );
      });
      return this.tareaService.getProcesos(resultsSize,pageIdx,filters.dateFrom,filters.dateTo,filters.search,extraParams,sort);
    }

    getFetch(resultsSize: number, pageIdx: number, sort?: SortDirection, filters?: GenericTableFilter): Observable<Page<Proceso>> {
      let extraParams = new Map<string,string>();
      filters.multipleOptions.forEach((val:TableFilter[],key )=>{
        extraParams
        .set(key,val.filter(filter=>filter.value==true)
          .map(obj=>obj.name)
          .join(',')
        );
      });
      return this.tareaService.getProcesos(resultsSize,pageIdx,filters.dateFrom,filters.dateTo,null,extraParams,sort);
    }

    getFilters(): Observable<GenericTableFilter> {
      return new Observable<GenericTableFilter>((subscribe)=>{
        let filter = new GenericTableFilter();

        this.tagService.getTags(999,0).subscribe(pageTags=>{
          let tags = pageTags.content;
          let tagsFilters = [];
          tags.forEach(tag=>{
            tagsFilters.push({name : tag.id.toString(),filterName: tag.name})
          })
          filter.multipleOptions.set("tags",tagsFilters);
          subscribe.next(filter);
          subscribe.complete();
        });
    
      });
     
    }

  nuevaTarea($event){
    let dialogRef = this.dialog.open(AbmTareasComponent,{
      height : 'auto'
    });
  }

  editarTarea($event){
    let dialogRef = this.dialog.open(AbmTareasComponent,{
      height : '50%',
      data : { trabajo : $event}
    });
  }

 

}
