import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-delete-all-confirm',
  templateUrl: './delete-all-confirm.component.html',
  styleUrls: ['./delete-all-confirm.component.css']
})
export class DeleteAllConfirmComponent implements OnInit {

  toDelete : string[] = [];
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    private matDialog : MatDialog) { 
    this.data.content2delete.forEach(obj=>{
      let tmpContent = '';
      Object.keys(obj).forEach(attr=>{
        tmpContent +=  obj[attr]+' | ';
      });
      this.toDelete.push(tmpContent);
    })
   
  }

  ngOnInit() {
  }

  closeDialog(){
    this.matDialog.closeAll();
  }

  confirmElimination(){
    this.data.confirm();
    this.closeDialog();
  }

}
