import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { GenericFile } from '../shared/generic-file';

@Component({
  selector: 'app-file-list',
  templateUrl: './file-list.component.html',
  styleUrls: ['./file-list.component.css']
})
export class FileListComponent implements OnInit {

  @Input()
  genericFiles : GenericFile[]=[];
  @Output()
  downloadEvent : EventEmitter<GenericFile>;
  @Output()
  shareEvent : EventEmitter<GenericFile>;
  @Output()
  helpEvent : EventEmitter<GenericFile>;

  constructor() { 
    this.downloadEvent = new EventEmitter<GenericFile>();
    this.shareEvent = new EventEmitter<GenericFile>();
    this.helpEvent = new EventEmitter<GenericFile>();
  }

  ngOnInit() {
  }

  downloadFile($event : GenericFile): void{
    this.downloadEvent.emit($event);
  }
  shareFile($event : GenericFile):void{
    this.shareEvent.emit($event);
  }
  helpFile($event : GenericFile):void{
    this.helpEvent.emit($event);
  }

}
