import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import {TareasComponent} from './tareas/tareas.component';
import { PrintTareaComponent } from './tareas/print-tarea/print-tarea.component';
import { MovimientosComponent } from './movimientos/movimientos.component';
import { ConfigComponent } from './config/config.component';
import { SideNavMainOutletComponent } from './side-nav-main-outlet/side-nav-main-outlet.component';
import { WelcomeComponent } from './welcome/welcome.component';


const routes: Routes = [
  {path: 'welcome',component: WelcomeComponent},
  {path: '',component: SideNavMainOutletComponent, children : [
    { path: 'trabajos', component: TareasComponent },
    { path: 'trabajos/1/print', component: PrintTareaComponent},
    /*movimientos*/
    { path: 'trabajos/:taskId/movimientos', component: MovimientosComponent },
    /*config*/
    { path: 'administrar', component: ConfigComponent },  
  ]}
  
];

@NgModule({
  imports: [ RouterModule.forRoot(routes, { useHash: true })],
  exports: [ RouterModule ]
})
export class AppRoutingModule { 
 
}
