export class Page<T> {
    public content : T[];
    public size : number;
    public number : number;
    public sort : any;
    public totalElements : number;
    public firstPage : boolean;
    public lastPage : boolean;


    constructor(){
        this.content = [];
        this.number = 0;
        this.size = 10;
        this.totalElements = 0;
        this.firstPage = true;
        this.lastPage = true;
    }
}
