import { Tag } from './tag';
import { GtColumn } from 'src/app/generic-table/decorators/decorators';

export class Proceso {
    @GtColumn("Id")
    public id : number;
    @GtColumn("Nombre")
    public nombre : string;
    @GtColumn("Descripcion")
    public descripcion:string;
    @GtColumn("Estado")
    public estado : ESTADO;
    public tags : Tag[];

    constructor(){
        this.id= null;
        this.nombre = null;
        this.descripcion = null;
        this.estado = null;
        
    }
    
}
export enum ESTADO{
    ESPERANDO_APROBACION,
    RECHAZADO,
    ACEPTADO,
    EN_PROCESO,
    FINALIZADO
}

