import { Injectable } from '@angular/core';
import { Account } from 'src/model/account';
import { Page } from 'src/app/generic-table/shared/page';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class UsuarioService {

  constructor(private http: HttpClient) { }

  public getUsuarios(limit : number,offset: number): Observable<Page<Account>>{
    
    return this.http.get<Page<Account>>(`${environment.api}/grupo/:grupo_id/accounts?limit=${limit}&offset=${offset}`);
    
  }
}
