import { Component, OnInit,Input, Output, EventEmitter, ViewChild, InputDecorator, ElementRef } from '@angular/core';
import { GenericFile } from '../shared/generic-file';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-file-manager',
  templateUrl: './file-manager.component.html',
  styleUrls: ['./file-manager.component.css']
})
export class FileManagerComponent implements OnInit {

  @Input()
  files : GenericFile[];

  @Output()
  downloadEvent : EventEmitter<GenericFile>;
  @Output()
  shareEvent : EventEmitter<GenericFile>;

  @Output()
  uploadEvent : EventEmitter<File>;

  details : GenericFile;
  fileDrag : boolean = false;
  @ViewChild("ifile",{static:false})
  input : ElementRef;
  constructor() { 
    this.downloadEvent = new EventEmitter<GenericFile>();
    this.shareEvent = new EventEmitter<GenericFile>();
    this.uploadEvent = new EventEmitter<File>();
  }

  ngOnInit() {
    window.addEventListener("dragover", e => {
      e && e.preventDefault();
    }, false);
    window.addEventListener("drop", e => {
      e && e.preventDefault();
    }, false);
  }
  drag(event){
    this.fileDrag = true;
  }

  fileDrop($event){
    this.fileDrag = false;
    const file = $event.dataTransfer.files[0];
    console.log(file);
    if(file.size == 4096 && file.type == ""){
      Swal.fire("Error !","Solo se permiten subir archivos.");
    }else{
      this.uploadEvent.emit(file);
    }
  }
  dragLeave($event){
    setTimeout(()=>{
        this.fileDrag = false;
    },3000);
  }

  handleFileInput(files){
    console.log(files);
    this.uploadEvent.emit(files[0]);
  }

  uploadFile(){
    this.input.nativeElement.click();
  }

  download($event:GenericFile):void{
    this.downloadEvent.emit($event);
  }
  share($event:GenericFile):void{
    this.shareEvent.emit($event);
  }
  helpEvent($event:GenericFile):void{
    this.details = $event;
  }
  closeDetails():void{
    this.details = null;
  }

}
