import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { Pageable } from 'src/model/pageable';
import { TagsService } from 'src/services/tags.service';
import { Page } from 'src/app/generic-table/shared/page';
import { Observable } from 'rxjs';
import { Tag, TAG_TIPO } from 'src/model/tag';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-tag',
  templateUrl: './tag.component.html',
  styleUrls: ['./tag.component.css']
})
export class TagComponent extends Pageable implements OnInit {

  /*INICIO TABLE OVERRIDE*/
  public dataService(limit: number, offset: number):Observable<Page<any>> {
    return this.tagService.getTags(limit,offset);
  }
  tagFormGroup : FormGroup;
  public displayedColumns: string[] = ["id","name","puntos","tipo","acciones"];
  /*FIN TABLE OVERRIDE*/

  tagAEditar : Tag = undefined;

  constructor(private fb : FormBuilder,private tagService : TagsService) { 
    super();
    this.tagFormGroup = this.fb.group({
      "nombre" : ['',[Validators.required,Validators.maxLength(50)]],
      "puntos" : ['',[Validators.required,Validators.min(0),Validators.pattern("[0-9]*")]],
      "cliente":[false,[]]
    });  
  }

  ngOnInit() {
    this.updateTable();
    this.onEditEvent.subscribe(editObj => {
      this.tagFormGroup.controls['nombre'].setValue(editObj.name);
      this.tagFormGroup.controls['puntos'].setValue(editObj.puntos);
      this.tagFormGroup.controls['cliente'].setValue(editObj.tipo);
      this.tagAEditar = editObj;
      console.log(editObj);
    })
  }

  cancelarEdicion(){
    this.tagAEditar = undefined;
    this.tagFormGroup.controls['nombre'].setValue("");
    this.tagFormGroup.controls['puntos'].setValue("");
    this.tagFormGroup.controls['cliente'].setValue("");
  }

  actualizarTag(){
    if(this.tagFormGroup.valid){
      let tagFormValidado = this.tagFormGroup.getRawValue();
      let tagApersistir = new Tag();
      tagApersistir.id = this.tagAEditar.id;
      tagApersistir.name = tagFormValidado.nombre;
      tagApersistir.puntos = tagFormValidado.puntos;
      tagApersistir.tipo = (tagFormValidado.cliente)?TAG_TIPO.CLIENTE:TAG_TIPO.SERVICIO;

      this.tagService.updateTag(tagApersistir).subscribe(tagDb =>{
        this.updateTable();
        this.cancelarEdicion();
        Swal.fire(
          'Tag Modificado',
          `El tag '${tagApersistir.name}' se modifico correctamente !`,
          'success'
        );
      });

    }
  }

  agregarNuevoTag(){
    if(this.tagFormGroup.valid){
      let tagFormValidado = this.tagFormGroup.getRawValue();
      let tagApersistir = new Tag();
      tagApersistir.name = tagFormValidado.nombre;
      tagApersistir.puntos = tagFormValidado.puntos;
      tagApersistir.tipo = (tagFormValidado.cliente)?TAG_TIPO.CLIENTE:TAG_TIPO.SERVICIO;

      this.tagService.createTag(tagApersistir).subscribe(tagDb =>{
        this.updateTable();
        this.cancelarEdicion();
        Swal.fire(
          'Nuevo Tag Agregado',
          `El tag '${tagApersistir.name}' se guardo correctamente !`,
          'success'
        );
      });
    }
  }

  delete(id:number){
    Swal.fire(
      {
      title: 'Desea eliminar el tag ?',
      text: "Esto no se podr revertir!",
      //icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Borrar'
    }).then((result) => {
      if (result.value) {
        Swal.fire(
          'Borrado!',
          'El tag ha sido eliminado.',
          'success'
        )
      }
    })
  }

}
