import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { TablePrintComponent } from './table-print/table-print.component';
import { WhiteOutlerComponent } from './white-outler/white-outler.component';

const routes: Routes = [
    {
        path: 'gtable',
        component : WhiteOutlerComponent,
        children:[
            { path: 'print', component: TablePrintComponent }
        ]
    }

];

@NgModule({
  imports: [ RouterModule.forChild(routes)],
  exports: [ RouterModule ]
})
export class GenericTableRouting { 
 
}
