import {} from "reflect-metadata";
import 'core-js/es7/reflect';

import {PROPERTY_COLUMN_METADATA_KEY} from "./constants";

export const GtColumn = (colName : string)=>{
    return (target : any, propertyKey?:string)=>{
        return Reflect.defineMetadata(PROPERTY_COLUMN_METADATA_KEY,colName,target,propertyKey);
    }
} 

export const GetTableColumns = (target: any, propertyKey? : string)=>{
    console.log("reflect metadata",PROPERTY_COLUMN_METADATA_KEY);
    return Reflect.getMetadata(PROPERTY_COLUMN_METADATA_KEY,target,propertyKey);
}