import { Component, OnInit } from '@angular/core';
import { Pageable } from 'src/model/pageable';
import { Observable } from 'rxjs';
import { Page } from 'src/app/generic-table/shared/page';
import { UsuarioService } from 'src/services/usuario.service';
import { MatDialog } from '@angular/material/dialog';
import { AbmUsuariosComponent } from './abm-usuarios/abm-usuarios.component';

@Component({
  selector: 'app-usuarios',
  templateUrl: './usuarios.component.html',
  styleUrls: ['./usuarios.component.css']
})
export class UsuariosComponent extends Pageable implements OnInit {
  
  public displayedColumns: string[] = ["id","username","email","roles","permisos","acciones"];
  public dataService(limit: number, offset: number): Observable<Page<any>> {
    return this.usuarioService.getUsuarios(limit,offset);
  }

  constructor(private usuarioService : UsuarioService,private dialog : MatDialog) {
    super();
   }

  ngOnInit() {
    this.updateTable();
  }

  agregarUsuario(){
    this.dialog.open(AbmUsuariosComponent);
  }

}
