import { Injectable } from '@angular/core';
import { Tag } from 'src/model/tag';
import { Observable } from 'rxjs';
import { Page } from 'src/app/generic-table/shared/page';
import { environment } from 'src/environments/environment';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class TagsService {

  constructor(private http: HttpClient) { }


  public getTags(limit : number,offset: number): Observable<Page<Tag>>{
    return this.http.get<Page<Tag>>(`${environment.api}/grupo/:grupo_id/tags?limit=${limit}&offset=${offset}`);
  }

  public createTag(tag : Tag): Observable<Tag>{
    return this.http.post<Tag>(`${environment.api}/grupo/:grupo_id/tags`,tag);
  }

  public updateTag(tag : Tag): Observable<Tag>{
    return this.http.put<Tag>(`${environment.api}/grupo/:grupo_id/tags`,tag);
  }

}
