import { Injectable } from '@angular/core';
import {ErrorDialogService} from './errorhandler.service';

import {
    HttpInterceptor,
    HttpRequest,
    HttpResponse,
    HttpHandler,
    HttpEvent,
    HttpErrorResponse
} from '@angular/common/http';

import { Observable, throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import { AccountService } from 'src/services/account.service';

@Injectable()
export class HttpConfigInterceptor implements HttpInterceptor {
    constructor(public errorDialogService: ErrorDialogService,private accountService:AccountService) { }
    intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    /*GRUPO ID INJECTOR**/
    if(request.url.includes(":grupo_id")){
        request = request.clone({
            url: request.url.replace(":grupo_id",this.accountService.getUserGroupId()),
            //headers: request.headers.set('Access-Control-Allow-Origin', '*'),
            //withCredentials:true     
        });
    }
    //request = request.clone({ headers: request.headers.set('Access-Control-Allow-Origin', '*'),withCredentials:true });

        /*
        -----------------------------
        Modifico requests pendientes !!!
        -----------------------------
        const token: string = localStorage.getItem('token');

        if (token) {
            request = request.clone({ headers: request.headers.set('Authorization', 'Bearer ' + token) });
        }

        if (!request.headers.has('Content-Type')) {
            request = request.clone({ headers: request.headers.set('Content-Type', 'application/json') });
        }

        request = request.clone({ headers: request.headers.set('Accept', 'application/json') });
        */
        return next.handle(request).pipe(
            map((event: HttpEvent<any>) => {
                if (event instanceof HttpResponse) {
                    console.log('event--->>>', event);
                    // this.errorDialogService.openDialog(event);
                }
                return event;
            }),
            catchError((error: HttpErrorResponse) => {
                let data = {};
                data = {
                    reason: error && error.error && error.error.reason ? error.error.reason : '',
                    status: error.status
                };
                this.errorDialogService.openDialog(data);
                return throwError(error);
            }));
    }
}