import { Component, OnInit, Inject } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { TagsService } from 'src/services/tags.service';
import { Proceso } from 'src/model/proceso';
import { ProcesoService } from 'src/services/tarea.service';
import Swal from 'sweetalert2';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { GenericFile, GenericFileType } from 'src/app/generic-file-manager/shared/generic-file';

@Component({
  selector: 'app-abm-tareas',
  templateUrl: './abm-tareas.component.html',
  styleUrls: ['./abm-tareas.component.css']
})

export class AbmTareasComponent implements OnInit {

  public abmTareaFormGroup : FormGroup;
  options = [];
  selectedOptions = [];
  nuevo : boolean = true;
  loadingTags : boolean;
  selected = this.selectedOptions;
  files : GenericFile[] = [];
  selectedTags : number[] = [];

  constructor(
    private fb: FormBuilder,
    private tagService: TagsService,
    private procesoService : ProcesoService,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) {
    this.files.push({fileName : "test",type : "txt",clasification : GenericFileType.IMAGE,fileIntegrity : "asdfas23423"});
    this.files.push({fileName : "test1",type : "jpg",clasification : GenericFileType.IMAGE,fileIntegrity : "4h676u67"});
    this.files.push({fileName : "test2",type : "txt",clasification : GenericFileType.IMAGE,fileIntegrity : "asdfdfghy57as23423"});
    this.files.push({fileName : "test3",type : "txt",clasification : GenericFileType.IMAGE,fileIntegrity : "ghghj665"});


    this.loadingTags = true;
    this.tagService.getTags(999,0).subscribe(pageTags=>{
      this.options = pageTags.content;
      this.loadingTags = false;
      //this.abmTareaFormGroup.controls['tagsSelect'].setValue(this.options);
    });

    this.abmTareaFormGroup =  this.fb.group({
      nombre : ['',[Validators.required,Validators.maxLength(50)]],
      descripcion : ['',[Validators.required,Validators.maxLength(255)]],
      estado: ['0',Validators.required],
      cantidad:['0',[Validators.required,Validators.min(0)]],
      tagsSelect :[null,[Validators.required]]
    });

    //Se crea por defecto en estado "0" que es 'NUEVO', solo se cambia al modificar
    this.abmTareaFormGroup.controls['estado'].disable();
    this.checkNewOrEdition();
  }

  private checkNewOrEdition(){
    if(this.data && this.data.trabajo){
      this.setFormToEdition();

    };
  }
  private setFormToEdition(){
    this.nuevo = false;
    this.abmTareaFormGroup.controls['estado'].enable();
  }

  save (){
    console.log("Selected tags",this.selectedTags);
    if(this.abmTareaFormGroup.valid ){
      let trabajo = new Proceso();
      trabajo.nombre = this.abmTareaFormGroup.controls['nombre'].value;
      trabajo.descripcion = this.abmTareaFormGroup.controls['descripcion'].value;
      trabajo.estado = this.abmTareaFormGroup.controls['estado'].value;
      trabajo.tags = [];
      this.selectedTags.forEach(selectedId=>{
        trabajo.tags.push({'id':selectedId});
      });
      this.procesoService.createProceso(trabajo).subscribe(proceso=>{
        Swal.fire(`Trabajo #${proceso.id}`, 'Trabajo guardado!', 'success');
      });//SIGESA
    }
  }

  ngOnInit() {

  }

  getSelectedOptions(selected) {
    this.selectedOptions = selected.map(e=>e.id);
  }








}
