import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';
import {Account} from 'src/model/account';
@Injectable({
  providedIn: 'root'
})
export class AccountService {
  private SESSION_STORAGE_USER_NAME = "SGPS_USER";

  constructor(private http: HttpClient) { }


  public getAccountDetails(): Observable<Account>{
    let simpleObservable = new Observable<Account>((observer) => {
    
      if(this.isUserInCache()){
        observer.next(this.getUserFromCache());
        observer.complete();
      }else{
        this.http.get<Account>(`${environment.api}/account/info`).subscribe(response=>{
          this.saveUserInCache(response);
          observer.next(response);
          observer.complete();
        })
      }
      // observable execution
  })
    return simpleObservable;
  }

  private isUserInCache():boolean{
    return sessionStorage.getItem(this.SESSION_STORAGE_USER_NAME)!=null;
  }
  private saveUserInCache(userAccount : Account){
    sessionStorage.setItem(this.SESSION_STORAGE_USER_NAME,JSON.stringify(userAccount));
  }
  private getUserFromCache():Account{
    return JSON.parse(sessionStorage.getItem(this.SESSION_STORAGE_USER_NAME));
  }

  /*account information*/
  public getUserGroupId(){
    return this.getUserFromCache().grupo.id;
  }

}
