import { Component, OnInit, Input, Inject } from '@angular/core';
import { TableFilter } from '../shared/table-filter';
import { MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';
import { GenericTableFilter } from '../shared/GenericTableFilter';

@Component({
  selector: 'app-table-filter',
  templateUrl: './table-filter.component.html',
  styleUrls: ['./table-filter.component.css']
})
export class TableFilterComponent implements OnInit {


  genericTableFilter : GenericTableFilter = new GenericTableFilter();
  constructor(@Inject(MAT_DIALOG_DATA) public data: any,private dialog : MatDialog) { 
    this.genericTableFilter = data.genericTableFilter;
  
  

  }

  ngOnInit() {
  }

  filterChange(checked : boolean,filter : TableFilter){
    filter.value = checked;
    console.log(checked);
  }

  removeFilter(filter:TableFilter){
    //Un filtro puede tener un valor true, false o null(sin filtrar)
    filter.value = null;
  }

  activateFilter(filter:TableFilter){
    filter.value = true;
  }
  
  minDateChange($event){
    this.genericTableFilter.dateFrom = $event.value;
  }

  maxDateChange($event){
    this.genericTableFilter.dateTo = $event.value;
  }

  currSelected(key:string){
    return this.data.selected.get(key);
  }


  multipleOptionsFilterChange($event,key){
    this.genericTableFilter.multipleOptions.forEach((value,key)=>{
      value.forEach(filter=>{filter.value = null});
    });

    $event.forEach(selected=>{
      this.genericTableFilter.multipleOptions.get(key).find(o=>o.name == selected.name).value = true;
    });  
  }

  limpiarFiltro():void{
    this.genericTableFilter.dateFrom = null;
    this.genericTableFilter.dateTo = null;
    this.genericTableFilter.filters.forEach(filtro=>{
      filtro.value = null;
    });
    this.genericTableFilter.search = null;
    this.genericTableFilter.multipleOptions.forEach((value,key)=>{
      this.data.selected.set(key,[]);
      value.forEach(o=>{
        o.value = null;
      });
    })
  }
  
  filterClose(){
    this.dialog.closeAll();
  }

}
