import { Component, OnInit, Input, Output,EventEmitter, ViewChild } from '@angular/core';
import { GenericFile, GenericFileType } from '../shared/generic-file';
import {  MatMenuTrigger } from '@angular/material';

@Component({
  selector: 'app-file',
  templateUrl: './file.component.html',
  styleUrls: ['./file.component.css']
})
export class FileComponent implements OnInit {

  @Input()
  genericFile : GenericFile;
  @Output()
  downloadEvent : EventEmitter<GenericFile>;
  @Output()
  shareEvent : EventEmitter<GenericFile>; 
  @Output()
  helpEvent : EventEmitter<GenericFile>;

  @ViewChild(MatMenuTrigger,{static:false}) contextMenu: MatMenuTrigger;

  constructor() {
    this.downloadEvent = new EventEmitter<GenericFile>();
    this.shareEvent = new EventEmitter<GenericFile>();
    this.helpEvent = new EventEmitter<GenericFile>();
  }

  ngOnInit() {
  }

  getIconFromFileType(){
    let resp = null;
    switch (this.genericFile.type.toLowerCase()) {
      case 'jpg':
        resp = FileIcons.photo;
        break;
      default:
          resp = FileIcons.insert_drive_file;
        break;
    }
    return resp;
  }

  download() : void{
    this.downloadEvent.next(this.genericFile);
  }

  share() : void{
    this.shareEvent.next(this.genericFile);
  }

  help() : void{
    this.helpEvent.next(this.genericFile);
  }

  abrirMenu($event){
    this.contextMenu.openMenu();
  }

}
export enum FileIcons{
  photo = "photo" ,
  insert_drive_file = "insert_drive_file",
  movie = "movie",
  volume_up = "volume_up",
  folder_open = "folder_open"
}
