import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { Observable, throwError, Subject } from 'rxjs';
import { Proceso } from 'src/model/proceso';
import { Page } from 'src/app/generic-table/shared/page';
import Swal from 'sweetalert2'
import { retry, catchError } from 'rxjs/operators';
import { SortDirection } from 'src/app/generic-table/shared/sort-direction';

@Injectable({
  providedIn: 'root'
})
export class ProcesoService {

  constructor(private http: HttpClient) { }


  public getProcesos(limit : number,offset: number, from? : Date, to? : Date, search? : String, paramsMap? : Map<string,string>,sort? : SortDirection): Observable<Page<Proceso>>{
    
    let params = new HttpParams()
    .set("limit",limit.toString())
    .set("offset",offset.toString())

    if(from && to){
      params = params.append("from",from.toString());
      params = params.append("to",to.toString());
      
    }

    if(search){
      params = params.append("search",search.toString())
    }

    if(paramsMap){
      paramsMap.forEach((value,key)=>{
        params = params.append(key,value);
      });
    }

    if(sort && sort.direction){
      params = params.append("sortBy",sort.active);
      params = params.append("sortDirection",sort.direction);
    }

    return this.http.get<Page<Proceso>>(`${environment.api}/grupo/:grupo_id/trabajos`,{params: params});
    
  }


  public createProceso(proceso : Proceso): Observable<Proceso>{
    return this.http.post<Proceso>(`${environment.api}/grupo/:grupo_id/trabajos`,proceso);
  }
  public updateProceso(proceso : Proceso,idProceso:number): Observable<Proceso>{
    return this.http.put<Proceso>(`${environment.api}/grupo/:grupo_id/trabajos/${idProceso}`,proceso);
  }

 
}
