import { Output, OnInit, EventEmitter } from '@angular/core';
import { Page } from '../app/generic-table/shared/page';
import { Observable } from 'rxjs';

export  abstract class Pageable {

    /*Events*/
    public onEditEvent : EventEmitter<any> = new EventEmitter<any>();
    //Busca siempre y cuando exista columna id !

    
    
    /*table*/
    public abstract displayedColumns : string[];
    public dataSource : any[] = [];

    
    /*Results*/
    public length : number = 0; 
    public pageSize : number = 5;
    public pageIndex : number = 0;
    public pageSizeOptions : number[]=[5, 10, 25, 100];

    public pageChange(event){
        this.pageSize = event.pageSize;
        this.pageIndex = event.pageIndex;
        /*results array*/
        this.updateTable();
    }

    public updateTable(){
        this.dataService(this.pageSize,this.pageIndex).subscribe((page)=>{
            this.dataSource = page.content;
            this.length = page.totalElements;
        });
    }

    public edit(id :number){
        let objAEditar = this.dataSource.find(a => a.id == id);
        this.onEditEvent.next(objAEditar);
    }

  

    /*Must override ! */
    public abstract dataService(limit: number, offset : number) : Observable<Page<any>>;

    
}
