import { Page } from './page';
import "reflect-metadata";
import { PROPERTY_COLUMN_METADATA_KEY } from '../decorators/constants';
import { ExampleTableObj } from './example-table-obj';
import { GetTableColumns } from '../decorators/decorators';
import { SortDirection } from './sort-direction';
import { TableFilter } from './table-filter';
import { GenericTableFilter } from './GenericTableFilter';
import { Observable } from 'rxjs';

export abstract class GenericTable<T> {

    private allMetadata;

    private columns : string[];
    public rows : Page<T>;
    private object : T;

    private type : (new () => T);
    constructor(type: (new () => T)){
        // 0) instantiate Obj to get decorators
        this.type = type;
        this.rows = new Page<T>();

    }
    public getColumns(): any[]{
        let tableColumnsTmp = [];
        let tableColumns = Object.keys(new this.type());
        console.log(tableColumns);
        tableColumns.forEach(attrKey =>{
            let colName = GetTableColumns(new this.type(),attrKey);
            if(colName){
                tableColumnsTmp.push({colName : colName,attr : attrKey});
            }
        })

        // 1) getting columns from T obj;
        this.setColumns(tableColumnsTmp);

        return tableColumnsTmp;
    }

    private setColumns(columns : string[]){
        console.log("detected columns : ",columns);
        this.columns = columns;
    }
    public getTableColumns() : string[]{
        return this.columns;
    }


    //Override part :B
    abstract searchFetch(search: string, resultsSize : number, pageIdx : number, sort? : SortDirection,filters? : GenericTableFilter) : Observable<Page<T>>;
    abstract getFetch(resultsSize : number, pageIdx : number, sort? : SortDirection,filters? : GenericTableFilter) : Observable<Page<T>>;
    abstract getFilters(): Observable<GenericTableFilter>;


}
