import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ExampleTableObj } from '../shared/example-table-obj';
import { GenericTable} from '../shared/generic-table';
import { TestService } from '../shared/test.service';
import {SelectionModel} from '@angular/cdk/collections';
import {MatTableDataSource} from '@angular/material/table';
import { GtColumn } from '../decorators/decorators';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
import { DeleteConfirmComponent } from '../delete-confirm/delete-confirm.component';
import { TableFilterComponent } from '../table-filter/table-filter.component';
import { DeleteAllConfirmComponent } from '../delete-all-confirm/delete-all-confirm.component';
import { TablePrintComponent } from '../table-print/table-print.component';
import { MatDialogConfig } from "@angular/material/dialog";
import { PrintData } from '../table-print/print-data';
import { Page } from '../shared/page';
import { Router } from '@angular/router';
import { PrintService } from '../shared/print.service';
import { SortDirection } from '../shared/sort-direction';
import { TableFilter } from '../shared/table-filter';
import { GenericTableFilter } from '../shared/GenericTableFilter';
import { Observable } from 'rxjs';
import { subscribeOn } from 'rxjs/operators';
import {RowAction} from '../shared/row-action';
import {ThemePalette} from '@angular/material/core';

export interface PeriodicElement {
  name: string;
  position: number;
  weight: number;
  symbol: string;
}


const DEFAULT_PAGE_SIZE = 10;
@Component({
  selector: 'app-table',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.css']
})
export class TableComponent implements OnInit {

  @Input()
  genericTable : GenericTable<any>;
  @Input()
  canSelectRows : boolean;
  @Input()
  withActions : boolean = true;
  @Input()
  withViewAction = true;
  @Input()
  withEditAction = true;
  @Input()
  withDeleteAction = true;
  @Input()
  withPagination = true;
  @Input()
  printData : PrintData;
  @Input()
  rowActions : RowAction[];
  @Output()
  addEvent : EventEmitter<any>;
  @Output()
  viewEvent : EventEmitter<any>;
  @Output()
  editEvent : EventEmitter<any>;
  @Output()
  deleteEvent : EventEmitter<any>;


  displayedColumns: string[] = [];
  elementData : any[] = [];
  attrToShow  : any[] = [];
  dataSource = new MatTableDataSource<PeriodicElement>(this.elementData);
  selection = new SelectionModel<PeriodicElement>(true, []);
  sort : SortDirection = new SortDirection();
  busqueda : string;
  isLoading : boolean = false;
  genericTableFilter : GenericTableFilter;

  constructor(
    private printService : PrintService,
    private matDialog : MatDialog,
    private route : Router){
    this.editEvent = new EventEmitter<any>();
    this.deleteEvent = new EventEmitter<any>();
    this.viewEvent = new EventEmitter<any>();
    this.addEvent = new EventEmitter<any>();
    this.genericTableFilter = new GenericTableFilter();
    
  }
  ngOnChange(){

  }

  ngOnInit(): void {
    //displaying initial content (getFetch)
    this.fetchNewData();

    //rendering columns
    if(this.canSelectRows==true && this.withActions){
      this.displayedColumns.push("select");
    }

    //Devuelve array de objeto {colName : "Nombre Aspirante", attr : "name"}
    let attributesToShow = this.genericTable.getColumns();

    this.attrToShow.push(...attributesToShow);

    this.displayedColumns.push(...attributesToShow.map(obj => obj.attr));

    if(this.withActions == true && this.withActions){
      this.displayedColumns.push("actions");
    }

    //Filters
    this.genericTable.getFilters().subscribe(filters=>{
      this.genericTableFilter.dateFrom = filters.dateFrom;
      this.genericTableFilter.dateTo = filters.dateTo;
      this.genericTableFilter.filters = filters.filters;
      this.genericTableFilter.multipleOptions = filters.multipleOptions;
      this.genericTableFilter.search = filters.search;
      
    });

  }

  matPaginatorChange(pageChange : PageEvent):void{
      this.genericTable.rows.number = pageChange.pageIndex;
      this.genericTable.rows.size = pageChange.pageSize;
      
      this.fetchNewData();
  }

  private fetchNewData(): void{
    this.isLoading = true;
    /**
     * You have to discriminate between search and a normal fetch for performance !
     */
     if(this.genericTableFilter.search){
      this.genericTable.searchFetch(
        this.busqueda,
        this.genericTable.rows.size,
        this.genericTable.rows.number,
        this.sort,
        this.genericTableFilter).subscribe(page=>{
          this.updateTable(page);
          this.isLoading = false;
        });
    }else{
      this.genericTable.getFetch(
        this.genericTable.rows.size,
        this.genericTable.rows.number,
        this.sort,
        this.genericTableFilter
      ).subscribe(page=>{
        this.updateTable(page);
        this.isLoading = false;
      })
    }
  }

  
  updateTable(page : Page<any>){

    this.genericTable.rows.content = page.content;
    this.genericTable.rows.firstPage = page.firstPage;
    this.genericTable.rows.lastPage = page.lastPage;
    this.genericTable.rows.number = page.number;
    this.genericTable.rows.size = page.size;
    this.genericTable.rows.totalElements = page.totalElements;

    this.dataSource.data = this.genericTable.rows.content;
  }

  sortData($event){
    
    this.sort.active = $event.active;
    this.sort.direction = $event.direction;

    this.fetchNewData(); 
  }

  viewClick(object : any){
    this.viewEvent.next(object);
  }

  editClick(object : any){
    this.editEvent.next(object);
  }

  deleteClick(object : any){
    this.matDialog.open(DeleteConfirmComponent,{
      data:{
        obj2delete : object,
        confirm : ()=>{
          alert("Eliminado roke");
        }
      }});
  }

  deleteAllClick(){
    this.matDialog.open(DeleteAllConfirmComponent,{
      data : {
        content2delete : this.selection.selected,
        confirm : ()=>{
          alert("Eliminado roke");
        }
      }
    })
  }

  printClick(){
    //detalles adicionales a la impresion (ademas del listado)
    if(!this.printData){
      this.printService.printData = new PrintData();
      this.printService.printData.title = "LISTADO";
      this.printService.printData.description = "";
      this.printService.printData.details = new Map<String,String>();
    }else{
      this.printService.printData = this.printData;
    }

    //Agrego info de filtros
    this.addFilterInfoToPrintData(this.genericTableFilter);
    //envio los resultados seleccionados a imprimir en otro GenericTable
    //printTable.rows.content = this.selection.selected;
    //Lo copio asi para que detecte el tipo generico utilizado -> GenericTable<Type>
    this.printService.printTable = this.genericTable;
    this.printService.printTable.getFetch = (...any)=> {
      let printTablePage = new Page<any>();
      printTablePage.content = this.selection.selected;
      printTablePage.firstPage = true;
      printTablePage.lastPage = true;
      printTablePage.size = 10;
      printTablePage.totalElements =6;
  
      return new Observable<Page<any>>(response =>{
          response.next(printTablePage);
          response.complete();
      });
    };
    this.route.navigate(['gtable','print']);  
  }

  addClick(){
    this.addEvent.next(true);
  }

  filterClick(){
    //bilding cache of selected objs
    let currSelected = new Map<String,String[]>();

    this.genericTableFilter.multipleOptions.forEach((value,key)=>{
      currSelected.set(key, value.filter(o=>o.value==true).map(o=>o.name));  
    });

    this.matDialog.open(TableFilterComponent,{
      data : {
        genericTableFilter : this.genericTableFilter,
        selected : currSelected
      }
    }).beforeClose().subscribe(test=>{
      //if some filter then searchFetch
      this.analizeFilters();
    })
  }

  addFilterInfoToPrintData(genericTableFilter : GenericTableFilter){
    if(this.printData){
      if(genericTableFilter.search)
        this.printData.details.set("Palabra clave",genericTableFilter.search);
      if(genericTableFilter.dateTo && genericTableFilter.dateFrom)
        this.printData.details.set("Fechas",genericTableFilter.dateFrom+'-'+genericTableFilter.dateTo);
      if(genericTableFilter.filters)
        genericTableFilter.filters.forEach(filter=>{
          if(filter.value)
            this.printData.details.set(filter.filterName,filter.value?"si":"no");
        })
    }
  }

  analizeFilters() : void{
    //Reset cur page to idx 0
    this.genericTable.rows.number = 0;
    this.fetchNewData();
  }

  filterCleanSearch(){
    this.genericTableFilter.search = null;
    this.analizeFilters();
  }

  filterCleanDates(){
    this.genericTableFilter.dateFrom = null;
    this.genericTableFilter.dateTo = null;
    this.analizeFilters();
  }

  filterCleanFilter(filter : TableFilter){
    filter.value = null;
    this.analizeFilters();
  }


  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
        this.selection.clear() :
        this.dataSource.data.forEach(row => this.selection.select(row));
  }

  /** The label for the checkbox on the passed row */
  checkboxLabel(row?: PeriodicElement): string {
    if (!row) {
      return `${this.isAllSelected() ? 'select' : 'deselect'} all`;
    }
    return `${this.selection.isSelected(row) ? 'deselect' : 'select'} row ${row.position + 1}`;
  }

}
