import { Component, OnInit } from '@angular/core';
import { Pageable } from 'src/model/pageable';
import { MovimientoService } from 'src/services/movimiento.service';
import { Observable } from 'rxjs';
import { Page } from 'src/app/generic-table/shared/page';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-movimientos',
  templateUrl: './movimientos.component.html',
  styleUrls: ['./movimientos.component.css']
})
export class MovimientosComponent extends Pageable implements OnInit {
  
  private procesoId : number ;

  public dataService(limit: number, offset: number): Observable<Page<any>> {
    return this.movimientoService.getMovimientos(this.procesoId,limit,offset);
  }
  
  public displayedColumns : string[] = ['id', 'descripcion', 'acciones'];


  constructor(
    private movimientoService : MovimientoService,
    private route : ActivatedRoute) {
    super(); 

    this.route.paramMap.subscribe( paramMap =>
      this.procesoId =+ paramMap.get('taskId')
    );
    console.log(this.procesoId);
  }

  ngOnInit() {
        
    this.updateTable();
  }

}
