import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Page } from 'src/app/generic-table/shared/page';
import { Movimiento } from 'src/model/movimiento';
import { environment } from 'src/environments/environment';
import { HttpClient } from '@angular/common/http';
import { AccountService } from './account.service';

@Injectable({
  providedIn: 'root'
})
export class MovimientoService {

  constructor(
    private http: HttpClient,
    private accountService : AccountService) { }

  public  getMovimientos(procesoId: number,limit : number,offset: number): Observable<Page<Movimiento>>{
    return this.http.get<Page<Movimiento>>(`${environment.api}/grupo/:grupo_id/procesos/${procesoId}/movimientos?limit=${limit}&offset=${offset}`);
          
  }
}
